*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
}

ul{
    list-style: none;
}

body {
    font-family: "Comfortaa";
    background: $main;
}

.container {
    @include container();
}

.row {
    @include row();
}

::after, ::before {
    font-family: "fontawesome";
}

.icon {
    &-film::before{
        content: "\f008";
        color: $white;
        font-weight: 300;
    }
    &-play::before {
        content: '\f04b';
        font-weight: 900;
        color: $white;
        position: absolute;
    }
    &-play::after{
        content: '\10f04b';
        font-weight: 900;
        color: $white;
        opacity: 0.4;
    }
    
    &-burger::before {
        content: "\f0c9";
        font-weight: 300;
    }
}

.btn {
    font-family: "Roboto";
    font-weight: 500;
    font-size: 14px;
    line-height: 12px;
    padding: 17px 48px;
    background: $orange;
    border-radius: 4px;
    margin-right: 30px;
    color: $black;
    display: inline-block;
    
    &:hover{
        background: $main;
        color: $white;
    }
}

.title {
    font-size: 36px;
    font-weight: 400;
    margin-top: 60px;
    margin-bottom: 48px;
    display: flex;
    &__link{
        color: $orange;
        text-transform: capitalize;
        &::after {
            content: '';
            height: 2px;
            background: $orange;
            display: block;
            margin-top: 2px;
        }
    }
}