
.films {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;    
    @include media($md) {
        grid-template-columns: repeat(2, 1fr);
    }
    @include media($xs) {
        grid-template-columns: repeat(1, 1fr);
    }
}

.film{
    position: relative;
    border-radius: 6px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    
    &__img {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    
    &__content{
        position: relative;
        background: $film;
        display: flex;
        flex-direction: column;
        align-items: center;
        color: $white;
        padding: 56px 15px 20px 15px;
        font-family: "Montserrat";
        flex-grow: 1;
    }
    
    &__link {
        line-height: 48px;
        font-size: 50px;
    }
    
    &__time {
        font-size: 14px;
        padding: 7px 28px;
        border: 1px solid $white;
        background: $time;
        border-radius: 9px;
        margin-top: 13px;
        margin-bottom: 32px;
    }
    
    &__title {
        font-size: 22px;
        font-weight: 400;
        margin-bottom: 13px;
        @include limit(1)
    }
    
    &__desc{
        font-family: "Roboto";
        font-size: 14px;
        line-height: 21px;
        @include limit(6);
    }
}
